          "p": "　　SmartPCC作为业务感知的智能管道，为电信运营商的全业务场景实施策略控制管理。其采用面向5G的服务化架构，实现敏捷部署、秒级弹性，加速业务创新；采用DevOps一体化运营模式，实现业务按需部署、自动化测试和快速上线；能迅速匹配千行百业的差异化需求，为用户提供极致业务体验。华为SmartPCC解决方案提供传统平台与云化平台的混合组网方式，保护运营商传统资产，保障网络功能向5G平滑演进。"
