          "p": "　　华为云核心网融合数据领域总经理许曦表示：“感谢业界对华为SmartPCC策略管理能力和服务化实践的认可。随着5G时代的到来，策略管理将在泛接入、多业务、多行业的端到端QoS保障上发挥更加关键的作用。我们将在商业模型、技术架构、演进策略等方面做到更好，帮助运营商构建灵活、敏捷、开放的统一策略控制中心，开启5G时代的新商业、新运营、新体验，驱动产业发展与商业成功。”"
